<template>
	<view class="container mms-page wechat-page">
		<!--  -->
		<view class="search-area">
			<view class="search-icon"  @click="showDrawer"><uni-icons custom-prefix="iconfont" color="#ffffff" type="icon-sousuo" size="24"></uni-icons></view>
			<view class="search-info">
				<view class="icon-scan" @click="onScan">
					<uni-icons custom-prefix="iconfont" color="#ffffff" type="icon-saoyisao" size="28"></uni-icons>
				</view>
			</view>
		</view>
		<view class="mms-list">
			<view class="section-title">
				<view class="section-title-text">物料列表</view>
				<view class="title-right-txt">查询结果：{{resListTotal}} 条记录</view>
				<view class="section-title-txt">搜索条件：{{ searchInfo }}</view>
			</view>
			<view class="list" v-if="resList.length > 0">
				<view class="list-item"  v-for="item in resList" :key="item.materialsNo">
					<view class="list-item-title">
						<view class="list-title-icon">
							<uni-icons custom-prefix="iconfont" color="#6d3b85" type="icon-renwumingcheng" size="18"> </uni-icons> 
						</view>
					   {{item.materialsName}}
					</view>
			        <view class="list-item-detail" @click="onOpenDetail(item.materialsNo)"><uni-icons custom-prefix="iconfont" color="#6d3b85" type="icon-right" size="24"></uni-icons></view>
					<view>物料编号：<view class="list-item-info">{{item.materialsNo}}</view></view>
					<view>规格型号：<view class="list-item-info">{{item.materialsModel}}</view></view>
					<view>库存数量：<view class="list-item-info">{{item.inStockNum}} {{item.materialsUnit}}</view></view>
				</view>
				<view v-if="showMore"><button class="more-btn" @click="onMore"> 更多  <uni-icons custom-prefix="iconfont" color="##6d3b85" type="icon-icon_duojixiala" size="18"></uni-icons></button></view>
			</view>
			<view class="list" v-else>
				<view class="record-nolist">没有可显示的数据</view>
			</view>
		</view>
		
		<uni-drawer ref="showRight" mode="right" width="260" :mask-click="false">
			<view class="mms-drawer-body">
				<view @click="closeDrawer" class="mms-close" type="primary">x</view>
				<view class="mms-drawer-search">
					<uni-forms :modelValue="searchData">
					    <uni-forms-item name="hobby" label="物料名称">
					        <uni-easyinput type="text" v-model="searchData.materialsName" placeholder="物料名称" />
					    </uni-forms-item>
					    <uni-forms-item name="hobby" label="物料编号">
					        <uni-easyinput type="text" v-model="searchData.materialsNo" placeholder="物料编号" />
					    </uni-forms-item>
					    <uni-forms-item name="hobby" label="规格型号">
					        <uni-easyinput type="text" v-model="searchData.materialsModel" placeholder="规格型号" />
					    </uni-forms-item>
					</uni-forms>
					<button class="mms-btn" @click="getProInfo">查 询</button>
				</view>
			</view>
		</uni-drawer>
	</view>
</template>

<script>
	const util = require('../../static/js/util.js');
	export default {
		data() {
			return {
				userState: {},
				searchData:{}, 
				searchInfo:'', // 搜索条件文本
			    resList:[],
				resListAll:[],
				resListTotal:0,
				interval: 20, // 每页显示条数
				pageNum: 20, // 当前显示条数
				showMore:false,
			}
		},
		created(){
			this.userState = util.xcStorage.getStorageSync('userState');
			this.getProInfo();
		},
		methods: { 
			// 根据物料编号获取物料信息
			onScan: function() {
				// 只允许通过相机扫码
				var _this = this;
				uni.scanCode({
				    onlyFromCamera: true,
				    success: function (res) {
						let materialsNo = util.xcUrlCode.getMaterialsNo(res.result, 'code');
						_this.searchData = {};
						_this.searchData.materialsNo = materialsNo;
						
						_this.getProInfo();
				    }
				});
			},
			// 根据物料编号获取物料信息
			getProInfo(){
				var _this = this;
				this.searchInfo = "";
				if(_this.searchData.materialsName){
					this.searchInfo = this.searchInfo + " " + _this.searchData.materialsName;
				}
				if(_this.searchData.materialsNo){
					this.searchInfo = this.searchInfo + " " + _this.searchData.materialsNo;
				}
				if(_this.searchData.materialsModel){
					this.searchInfo = this.searchInfo + " " + _this.searchData.materialsModel;
				}
				
				const url = util.xcRequestURL.WAREHOUSE.getMaterialList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,_this.searchData, function(res) {
					// const { result } = res;
					if(res.status == 200){
						let resList = res.result;
						var thisList = [];
						if(resList.length > 0){ 
						    // _this.resList = res.result;
							for(var i=0;i<resList.length ;i++){
								resList[i].inStockNum = parseFloat(resList[i].inStockNum);
								if(resList[i].inStockNum > 0){
									thisList.push(resList[i]);
								}
							}
							_this.resListAll = thisList;
							_this.resListTotal = thisList.length;
							_this.$refs.showRight.close();
							_this.getListShow();
						}
					}
				});
			},
			showDrawer() {
			    this.$refs.showRight.open();
			},
			closeDrawer() {
			    this.$refs.showRight.close();
			},
			onOpenDetail(code){
				let pageUrl='/pages/stockDetail/stockDetail?code='+code;
				uni.navigateTo({
				  url: pageUrl,
				  // events: {
				  //   // 为指定事件添加一个监听器，获取被打开页面传送到当前页面的数据
				  //   acceptDataFromOpenedPage: function(data) {
				  //     console.log(data)
				  //   },
				  //   someEvent: function(data) {
				  //     console.log(data)
				  //   }
				  // }
				});
			},
			// 更多操作
			onMore: function(e){
			  this.getListShow();
			},
			getListShow(){
				const l1 = this.resListTotal;
				if (this.pageNum < l1) {
				  this.resList = this.resListAll.slice(0, this.pageNum);
				  this.pageNum = this.pageNum + this.interval;
				  this.showMore = true;
				} else {
				  this.resList = this.resListAll.slice(0, l1);
				  this.pageNum = l1;
				  this.showMore = false;
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "../../static/css/style.css";
	
</style>
